const block0 = (context, tags, suffix) => html`<${scopeTag("ui5-responsive-popover", tags, suffix)} horizontal-align="Center" placement-type="Bottom" aria-label="${ifDefined(context.actionSheetStepsText)}" class="${classMap(context.classes.popover)}" @ui5-after-close=${ifDefined(context._afterClosePopover)} content-only-on-desktop prevent-focus-restore with-padding _hide-header><ul class="ui5-wizard-responsive-popover-list">${ repeat(context._groupedTabs, (item, index) => item._id || index, (item, index) => block1(item, index, context, tags, suffix)) }</ul><div slot="footer" class="ui5-responsive-popover-footer"><${scopeTag("ui5-button", tags, suffix)} design="Transparent" @click="${context._closeRespPopover}">Cancel</${scopeTag("ui5-button", tags, suffix)}></div></${scopeTag("ui5-responsive-popover", tags, suffix)}>`;
const block1 = (item, index, context, tags, suffix) => html`<li><${scopeTag("ui5-button", tags, suffix)} icon="${ifDefined(item.icon)}" ?disabled="${item.disabled}" design="Transparent" data-ui5-header-tab-ref-id="${ifDefined(item.accInfo.ariaPosinset)}" @click="${context._onOverflowStepButtonClick}">${ifDefined(item.titleText)}</${scopeTag("ui5-button", tags, suffix)}></li>`;


